html {
  font-size: 10px;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  display: inline-block;
}

body {
  font-size: 10px;
  color: #000;
  font-family: "Lato", sans-serif;
}

.container {
  max-width: 120rem;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.container-content {
  max-width: 97rem;
  margin: 0 auto;
}

.section-line {
  border-bottom: 0.1rem solid rgba(208, 212, 216, 0.5);
}

.title {
  font-family: Georgia;
  font-weight: 400;
  font-size: 3.2rem;
  line-height: 3.8rem;
  text-align: center;
  color: #4E5665;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.header__top {
  background-image: url("../img/header-bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  padding: 4rem;
}

.header__top-inner {
  border: 2px solid #ECB84C;
  border-bottom: none;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.header__title {
  font-family: Georgia;
  font-weight: 700;
  font-size: 20rem;
  line-height: 22.7rem;
  color: #FFFFFF;
  text-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.5);
  margin: auto;
}

.header__text {
  font-family: Georgia;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 2.7rem;
  text-align: center;
  color: #FFFFFF;
  text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.5);
  display: flex;
  white-space: nowrap;
  align-items: center;
  margin-bottom: -1.2rem;
}

.header__text::before,
.header__text::after {
  content: "";
  border-top: 2px solid #ECB84C;
  width: 100%;
}

.header__text::after {
  margin-left: 3rem;
}

.header__text::before {
  margin-right: 3rem;
}

.menu {
  border-bottom: 0.1rem solid rgba(208, 212, 216, 0.5);
}

.menu__list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu__list-link {
  padding: 2.4rem 1rem 2rem;
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 1.9rem;
  color: #909090;
  margin: 0 0.6rem;
  border-bottom: 4px solid transparent;
  transition: all 0.5s;
}
.menu__list-link:hover {
  color: #4E5665;
  border-bottom: 4px solid #ECB84C;
}

.reason {
  padding-top: 8rem;
}

.reason__title {
  margin-bottom: 2.2rem;
}

.reason__text {
  font-family: Lato;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: #505050;
}

.reason__link {
  text-align: right;
}

.reason__link a {
  font-family: Lato;
  font-weight: 700;
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: #ECB84C;
  display: inline-block;
  margin-bottom: 3.6rem;
}

.reason__statistic {
  display: flex;
  justify-content: space-around;
  padding-bottom: 8rem;
}

.reason__statistic-img {
  margin-bottom: 3.2rem;
}

.reason__statistic-num {
  font-family: Lato;
  font-weight: 700;
  font-size: 2.2rem;
  line-height: 3rem;
  color: #4E5665;
}

.reason__statistic-text {
  font-family: Lato;
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: #909090;
}

.about {
  padding: 8rem 0;
}

.about__title {
  margin-bottom: 1.3rem;
}

.about__text {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: #505050;
  margin-bottom: 6.4rem;
}

.about__item {
  padding: 7rem 0;
  margin-bottom: 10rem;
}

.about__item:nth-child(odd) {
  background-position: right;
  background-repeat: no-repeat;
}

.about__item:nth-child(even) {
  background-position: left;
  background-repeat: no-repeat;
}

.about__box {
  max-width: 47rem;
  padding: 3.3rem 4rem 1.8rem;
  position: relative;
  background-color: #fff;
}

.about__box::before,
.about__box::after {
  content: "";
  position: absolute;
  width: 3.3rem;
  height: 3.3rem;
}

.about__item:nth-child(odd) .about__box {
  margin-right: auto;
}

.about__item:nth-child(even) .about__box {
  margin-left: auto;
}

.about__item:nth-child(odd) .about__box::before {
  border-top: 0.2rem solid #ECB84C;
  border-left: 0.2rem solid #ECB84C;
  top: 1.9rem;
  left: 1.9rem;
}

.about__item:nth-child(odd) .about__box::after {
  border-bottom: 0.2rem solid #ECB84C;
  border-right: 0.2rem solid #ECB84C;
  bottom: 1.9rem;
  right: 1.9rem;
}

.about__item:nth-child(even) .about__box::before {
  border-bottom: 0.2rem solid #ECB84C;
  border-left: 0.2rem solid #ECB84C;
  bottom: 0.9rem;
  left: 1.9rem;
}

.about__item:nth-child(even) .about__box::after {
  border-top: 0.2rem solid #ECB84C;
  border-right: 0.2rem solid #ECB84C;
  top: 1.9rem;
  right: 1.9rem;
}

.about__box-title {
  font-family: Georgia;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 2.5rem;
  color: #4E5665;
  margin-bottom: 2rem;
}

.about__box-text {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: #505050;
}

.about__review {
  padding-top: 2rem;
}

.about__review-title {
  margin-bottom: 3.8rem;
}

.about__preview-text {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: #505050;
  margin: 3rem auto 8rem;
}

.about__images-box {
  padding-top: 7.3rem;
  position: relative;
}

.about__images-box img {
  max-width: 100%;
}

.about__dot {
  width: 3.2rem;
  height: 3.2rem;
  border-radius: 50%;
  background-color: #ECB84C;
  mix-blend-mode: normal;
  opacity: 0.75;
  cursor: pointer;
  position: absolute;
}

.about__dot--1 {
  top: 19rem;
  left: 14.8rem;
}

.about__dot--2 {
  top: 18.8rem;
  left: 49.5rem;
}

.about__dot--3 {
  top: 14.7rem;
  right: 37.2rem;
}

.about__dot--4 {
  bottom: 2.8rem;
  right: 29.9rem;
}

.about__dot::after {
  content: "";
  position: absolute;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #FFD600;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.about__dot::before {
  content: "";
  position: absolute;
  background-image: url(../img/line.svg);
  width: 24.4rem;
  height: 12.2rem;
  top: -11.3rem;
  left: 2.5rem;
  display: none;
}

.about__dot-text {
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 2.8rem;
  color: #505050;
  position: absolute;
  top: -14rem;
  left: 15rem;
  white-space: nowrap;
  display: none;
}

.about__dot:hover::before {
  display: block;
}

.about__dot:hover .about__dot-text {
  display: block;
}

.photo {
  padding-bottom: 8rem;
}

.photo__title {
  margin-bottom: 4.2rem;
}

.photo__text {
  margin-bottom: 5.6rem;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: #505050;
}

.photo__slider {
  max-width: 64rem;
  margin: 0 auto;
  filter: drop-shadow(0px 2px 24px rgba(0, 0, 0, 0.3));
}

.slick-arrow {
  padding: 1.2rem;
  border: none;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  background-color: #ECB84C;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 5;
}

.slick-prev {
  left: -2rem;
}

.slick-next {
  right: -2rem;
}

.footer {
  padding: 5.6rem 0 3.2rem;
  background-color: #F8F8F8;
}

.footer__items {
  display: flex;
  justify-content: space-between;
}

.footer__item {
  margin-bottom: 4rem;
  position: relative;
}

.footer__list-item--icons {
  padding-left: 2.4rem;
  position: relative;
}

.footer__list-item--icons-interface::before {
  content: "";
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  background-image: url("../img/interface.svg");
  top: 0.2rem;
  left: 0;
}

.footer__list-item--icons-fb::before {
  content: "";
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  background-image: url("../img/facebook.svg");
  top: 0.2rem;
  left: 0;
}

.footer__list-item--icons-instagram::before {
  content: "";
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  background-image: url("../img/instagram.svg");
  top: 0.2rem;
  left: 0;
}

.footer__list-item--icons-youtube::before {
  content: "";
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  background-image: url("../img/youtube.svg");
  top: 0.2rem;
  left: 0;
}

.footer__list-item--icons-email::before {
  content: "";
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  background-image: url("../img/email.svg");
  top: 0.2rem;
  left: 0;
}

.footer__list-title {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 2rem;
  color: #4E5665;
  margin-bottom: 2rem;
}

.footer__list-link {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.9rem;
  color: #505050;
  margin-bottom: 0.8rem;
}

.copyright {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.9rem;
  color: #505050;
  display: block;
  margin: 0 auto;
  text-align: center;
}

.menu__btn {
  display: none;
}

/* === DESKTOP FIRST === */
/* xl <= Extra extra large (xxl) */
/* lg <= Extra large (xl) */
@media (max-width: 1075px) {
  .about__dot--1 {
    top: 15rem;
    left: 7.5rem;
  }

  .about__dot--2 {
    top: 18.8rem;
    left: 34rem;
  }

  .about__dot--3 {
    top: 13.3rem;
    right: 26rem;
  }

  .about__dot--4 {
    bottom: 2.8rem;
    right: 20rem;
  }
}
/* md <= Large (lg) */
@media (max-width: 840px) {
  .about__item {
    padding: 38.2rem 0 7rem;
    background-position: top;
    margin-bottom: 0;
  }

  .about__box {
    margin: 0 auto;
  }

  .about__item:nth-child(odd) {
    background-position: top;
    background-repeat: no-repeat;
  }

  .about__item:nth-child(even) {
    background-position: top;
  }

  .about__dot {
    display: none;
  }

  .about__image-box {
    padding-top: 0;
  }
}
/* sm <= Medium (md) */
@media (max-width: 700px) {
  .header__title {
    font-size: 120px;
    line-height: 150px;
  }

  .header__text {
    font-size: 20px;
  }

  .menu {
    position: relative;
  }

  .menu__btn {
    display: block;
    width: 4rem;
    cursor: pointer;
    position: absolute;
    top: 2rem;
    right: 4rem;
  }

  .menu__btn span {
    display: block;
    margin-bottom: 1rem;
    background-color: #ECB84C;
    height: 0.4rem;
  }

  .menu__list {
    display: none;
    padding-top: 5rem;
    text-align: center;
    border-bottom: 0.4rem solid #ECB84C;
  }

  .menu__list-link {
    display: block;
    border-top: 0.4rem solid #ECB84C;
  }

  .menu__list-link:hover {
    border-bottom: 0.4rem solid transparent;
  }

  .slick-prev {
    left: 2rem;
  }

  .slick-next {
    right: 2rem;
  }

  .photo__slider .slick-dots {
    display: flex;
    justify-content: center;
    list-style: none;
    margin-top: 40px;
  }

  .photo__slider .slick-dots button {
    font-size: 0;
    border: none;
    margin: 0 10px;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    background-color: #C4C4C4;
    cursor: pointer;
  }

  .photo__slider .slick-active button {
    background-color: #ECB84C;
  }

  .footer__items {
    display: block;
  }

  .footer__item {
    width: 100%;
    padding-right: 0;
  }

  .footer__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
/* xs <= Small (sm) */
@media (max-width: 540px) {
  .title {
    font-size: 2.4rem;
    line-height: 2.8rem;
  }

  .header__title {
    font-size: 10.2rem;
  }

  .header__text {
    display: none;
  }

  .header__top-inner {
    border-bottom: 0.2rem solid #ECB84C;
  }

  body {
    font-size: 1.5rem;
    line-height: 2.4rem;
  }

  .reason__statistic {
    display: block;
    padding-bottom: 4rem;
  }

  .reason__statistic-item {
    text-align: center;
    margin-bottom: 3.5rem;
  }

  .reason__statistic-img {
    text-align: center;
    margin: 0 auto;
    margin-bottom: 1.5rem;
  }

  .about {
    padding-top: 4rem;
  }

  .footer__list-item--icons::before {
    top: 0.4rem;
  }
}
@media (max-width: 425px) {
  .header__title {
    font-size: 7.2rem;
  }
}
@media (max-width: 375px) {
  .header__title {
    font-size: 7.2rem;
  }
}
@media (min-width: 700px) {
  .menu__list {
    display: flex !important;
  }
}